/* Timeline */
#pp-timeline {
  position: relative;
}
#pp-timeline::before {
  content: '';
  position: absolute;
  left: 10px;
  top: 0px;
  width: 2px;
  height: 100%;
  background-color: #000;
}
#pp-timeline ul {
  padding: 0 0 0 25px;
  text-align: left;
  list-style: none;
}
#pp-timeline .pp-item > span {
  -moz-transition: all 0.5s ease-in-out;
  -o-transition: all 0.5s ease-in-out;
  -webkit-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out;
  position: relative;
  display: inline-block;
  max-width: 100%;
  line-height: 2em;
  vertical-align: middle;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  cursor: pointer;
}
#pp-timeline .pp-item > span:hover, #pp-timeline .pp-item > span.currentSection {
  color: #000;
}

/* Pointer */
#pp-pointer {
  -moz-transition: all 0.5s ease-in-out;
  -o-transition: all 0.5s ease-in-out;
  -webkit-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out;
  -moz-border-radius: 50%;
  -webkit-border-radius: 50%;
  border-radius: 50%;
  position: absolute;
  left: 5px;
  top: 0px;
  width: 12px;
  height: 12px;
  background-color: #000;
}
#pp-pointer.seeking {
  -moz-animation: seeking_animate 0.7s infinite alternate linear;
  -webkit-animation: seeking_animate 0.7s infinite alternate linear;
  animation: seeking_animate 0.7s infinite alternate linear;
}

@-moz-keyframes seeking_animate {
  0% {
    opacity: 0;
    filter: blur(2px);
  }
  100% {
    opacity: 1;
    filter: blur(0px);
  }
}
@-webkit-keyframes seeking_animate {
  0% {
    opacity: 0;
    -webkit-filter: blur(2px);
    filter: blur(2px);
  }
  100% {
    opacity: 1;
    -webkit-filter: blur(0px);
    filter: blur(0px);
  }
}
@keyframes seeking_animate {
  0% {
    opacity: 0;
    -webkit-filter: blur(2px);
    filter: blur(2px);
  }
  100% {
    opacity: 1;
    -webkit-filter: blur(0px);
    filter: blur(0px);
  }
}
